/* roboto-regular - latin_cyrillic */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"),
    url("../fonts/roboto-regular.woff2") format("woff2"), /* Super Modern Browsers */
    url("../fonts/roboto-regular.woff") format("woff"), /* Modern Browsers */
}

